* {
    margin: 0;
    padding: 0;
    border: none !important;
    font-family: 'Baloo Bhai 2', sans-serif;
}

.main {
    width: 100%;
    display: flex;
    flex-direction: row;
    border: 1px solid red;
    height: 100rem;
    padding: 2px;
    gap: 2px;
}

.left {
    width: 17%;
    height: 100rem;
    border: 2px dashed blue;
    background-color: #1e2640;
    color: white;
}

.right {
    width: 83%;
    border: 2px dashed green;
    display: flex;
    flex-direction: column;
    padding: 2px;
    gap: 2px;
}

.header {
    width: 100%;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 97%;
    margin: 0rem auto;
}

.overview {
    height: 12rem;
    border: 2px solid red;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 97%;
    margin: 2rem auto;
}

.overview-card {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 70%;
    width: 100%;
    border: 2px solid red;
}

.overview-text {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.transaction {
    height: max-content;
    /* border: 2px solid red; */
    width: 97%;
    margin: 2rem auto;
    box-shadow: rgba(0, 0, 0, 0.18) 0px 2px 4px;
    padding: 1rem;
}

.store {
    align-items: center;
    display: flex;
    padding: 0.4rem;
    background-color: #12182a;
}

.logo img {
    margin: 0.1rem;
    width: 4rem;
    filter: invert(100%);
}

.store-name {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 1rem;
    color: white;
}

.store-name a {
    color: white;
    text-decoration: none;

}

.store-name p {
    margin: 0;
    font-size: 1.6rem;
    color: white;
    text-decoration: none;
}

.arrow {
    font-size: 1.7rem;
}
.nav-item{
    padding: 0 !important;
}
.nav-item ul {
    margin-top: 4rem;
    list-style: none;
    /* color: white; */
}

.nav-item ul li {
    list-style: none;
    /* color: white; */
    margin: 1.5rem;
    font-size: 1.2rem;
    border: 2px solid red;
}
.nav-item ul li:hover, .active {
    background-color: #343c53;
    padding: 0.5rem;
    border-radius: 0.9rem;
    transition: all 0.6s;
}

.nav-item ul li a {
    text-decoration: none;
    color: #ccced3;
}

.nav-icons {
    margin-right: 1.7rem;
    font-size: 1.6rem;
    border: 2px solid red;
    color: #ccced3;
}

.search-box {
    width: 40rem !important;
    border-radius: 13px !important;
    border: none !important;
}

.header-text {
    font-size: 1.2rem;
    display: flex;
    border: 1px solid red;
    align-items: center;
    justify-content: space-between;
    width: 20%;
}

.header-icons {
    /* border: 2px solid red; */
    width: 8%;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.header-icons-child {
    margin: 0.2rem;
    width: 3rem;
    height: 3rem;
    font-size: 1.5rem !important;
    background-color: #cacaca96;
    border-radius: 50%;
    padding: 0.6rem;

}

.card {
    width: 30%;
    height: 95%;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    padding: 1rem;
}

.text-right {
    /* border: 2px solid red; */
    text-align: right;
    width: 8rem;
}

.text-left {
    border: 2px solid red;
    text-align: left;
    width: 4rem;
}

.text-left a {
    text-decoration: none;
}

.transaction-search {
    display: flex;
    justify-content: space-between;
    width: 100% !important;
    height: 2rem;
    margin-top: 4.3rem;
}

.transaction-search-box {
    width: 85% !important;
    height: 2rem !important;
}

.transaction-button {
    width: 7rem;
    height: 3rem;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    padding: 0.5rem;
    font-size: 1.3rem;
}

.transaction-button2 {
    width: 3rem;
    height: 3rem;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    padding: 0.8rem;
}

button {

    border: none;
    padding: 0;
    margin: 0;
    background: none;
    text-decoration: none;
    /* In case it behaves like a link */
    cursor: pointer;
    /* Ensure cursor looks like a pointer for better UX */
    outline: none;

}

.transaction-table {
    margin-top: 2rem;
}

.no-border-table {
    border-collapse: collapse;
    border-spacing: 0;
}

.no-border-table th,
.no-border-table td {
    border: none;
    padding: 0;
}

.table-spacing td,
.table-spacing th {
    font-size: 1.1rem;
    padding: 1.4rem;
    /* Adjust the padding value as needed */
}